import AppHeader from '../views/AppHeader/AppHeader';
import AppContainer from '../views/AppContainer/AppContainer';
import AppTools from '../views/AppTools/AppTools';
import AppFrameList from '../views/AppFrameList/AppFrameList';
import AppMainCanvas from '../views/AppMainCanvas/AppMainCanvas';
import AppAnimationPlayer from '../views/AppAnimationPlayer/AppAnimationPlayer';

export default class AppControl {
  constructor(className) {
    this.className = className;
    this.currentTool = 'pen';
    this.dataImage = null;
    this.count = 0;
    this.frequency = 12;
  }

  start() {
    const header = new AppHeader('page-header');
    header.render();
    const container = new AppContainer('container');
    container.render();
    const tools = new AppTools('tools', container.block);
    tools.render();
    const frameList = new AppFrameList('frame-list', container.block);
    frameList.render();
    const mainCanvas = new AppMainCanvas('main-canvas', container.block);
    mainCanvas.render();
    frameList.block.addEventListener('click', AppControl.eventFrameList.bind(frameList));
    mainCanvas.block.querySelector('.canvas-prev').addEventListener('mousemove', (e) => {
      const context = e.target.getContext('2d');
      context.clearRect(0, 0, e.target.width, e.target.height);
      const x = Math.floor(e.offsetX / (e.target.clientWidth / e.target.width));
      const y = Math.floor(e.offsetY / (e.target.clientHeight / e.target.height));
      context.fillStyle = '#8885';
      context.fillRect(x, y, 1, 1);
      mainCanvas.block.querySelector('.canvas-prev').addEventListener('mouseleave', () => context.clearRect(0, 0, e.target.width, e.target.height));
    });
    const animationPlayer = new AppAnimationPlayer('animation-player', container.block, { width: 20, height: 20 });
    animationPlayer.render();
    animationPlayer.block.addEventListener('input', (e) => {
      const frequency = e.target.value;
      const prevElem = e.target.previousElementSibling;
      prevElem.innerText = `FPS: ${frequency}`;
      this.frequency = frequency;
    });
    animationPlayer.block.addEventListener('click', (e) => {
      if (e.target.className.split('__')[1] === 'full-size') {
        animationPlayer.block.requestFullscreen();
      }
    });
    this.dataImage = AppControl.getFramesData();
    const playAnimation = () => {
      const framesData = this.dataImage;
      const imageData = framesData[this.count % framesData.length];
      animationPlayer.block.getElementsByTagName('canvas')[0].getContext('2d').putImageData(imageData, 0, 0);
      this.count += 1;
      setTimeout(playAnimation, 1000 / this.frequency);
    };
    setTimeout(playAnimation, 1000 / this.frequency);
    mainCanvas.block.querySelector('.canvas-prev').addEventListener('mousedown', () => {
      mainCanvas.block.querySelector('.canvas-prev').addEventListener('mousemove', AppControl.draw);
      mainCanvas.block.querySelector('.canvas-prev').addEventListener('mouseup', () => {
        this.dataImage = AppControl.getFramesData();
        mainCanvas.block.querySelector('.canvas-prev').removeEventListener('mousemove', AppControl.draw);
      });
      mainCanvas.block.querySelector('.canvas-prev').addEventListener('mouseleave', () => {
        this.dataImage = AppControl.getFramesData();
        mainCanvas.block.querySelector('.canvas-prev').removeEventListener('mousemove', AppControl.draw);
      });
    });
  }

  static draw(e) {
    const x = Math.floor(e.offsetX / (e.target.clientWidth / e.target.width));
    const y = Math.floor(e.offsetY / (e.target.clientHeight / e.target.height));
    const canvas = document.querySelector('.canvas-image');
    const context = canvas.getContext('2d');
    const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
    const currFrame = document.querySelector('.frame-list__curr-frame').getElementsByTagName('canvas')[0].getContext('2d');
    currFrame.putImageData(imageData, 0, 0);
    context.fillStyle = '#000';
    context.fillRect(x, y, 1, 1);
  }

  static eventFrameList(e) {
    if (e.target.tagName !== 'SECTION') {
      let elem = e.target;
      while (elem.tagName !== 'DIV') {
        elem = elem.parentElement;
      }
      if (elem.className.split('__')[1] === 'add-frame') {
        this.createFrame();
        const canvas = document.querySelector('.frame-list__curr-frame').getElementsByTagName('canvas')[0];
        const imageData = canvas.getContext('2d').getImageData(0, 0, canvas.width, canvas.height);
        AppControl.updateMainCanvas(imageData);
      } else if (elem.classList[0].split('__')[1] === 'frame') {
        if (e.target.className.split('__')[1] === 'copy') {
          this.createCopyFrame(elem);
          const canvas = elem.getElementsByTagName('canvas')[0];
          const data = canvas.getContext('2d').getImageData(0, 0, canvas.width, canvas.height);
          AppControl.updateMainCanvas(data);
          this.updateCount();
        } else if (e.target.className.split('__')[1] === 'remove') {
          let newCurrentFrame;
          if (elem.previousSibling) {
            newCurrentFrame = elem.previousSibling;
          } else if (elem.nextSibling && elem.nextSibling.className !== 'frame-list__add-frame') {
            newCurrentFrame = elem.nextSibling;
          } else if (elem.nextSibling.className === 'frame-list__add-frame') {
            newCurrentFrame = this.createFrame();
          }
          this.togletCurrFrame(newCurrentFrame);
          const canvas = newCurrentFrame.getElementsByTagName('canvas')[0];
          const imageData = canvas.getContext('2d').getImageData(0, 0, canvas.width, canvas.height);
          AppControl.updateMainCanvas(imageData);
          elem.remove();
          this.updateCount();
        } else {
          this.togletCurrFrame(elem);
          const canvas = elem.getElementsByTagName('canvas')[0];
          const imageData = canvas.getContext('2d').getImageData(0, 0, canvas.width, canvas.height);
          AppControl.updateMainCanvas(imageData);
        }
      }
    }
  }

  static updateMainCanvas(imageData) {
    const canvas = document.querySelector('.canvas-image');
    const context = canvas.getContext('2d');
    context.putImageData(imageData, 0, 0);
  }

  static getFramesData(frameList) {
    const list = frameList || document.querySelector('.frame-list');
    const data = [];
    for (let i = 0; i < list.children.length - 1; i += 1) {
      const canvas = list.children[i].getElementsByTagName('canvas')[0];
      const context = canvas.getContext('2d');
      data.push(context.getImageData(0, 0, canvas.width, canvas.height));
    }
    return data;
  }
}
